.profile {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (max-width: 768px) {
    .profile {
        display: block;
    }
}

@media screen and (max-width: 600px) {
    .profile {
        margin-top: 38px;
    }
}